* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --link-color: #42B883;
    --main-bg: #f1f1f1;
    --nav-bg: #010001;
}

#root {
    height: 100%;
}

html {
    height: 100%;
    font-family: 'Open Sans', 'sans-serif';
    font-size: 16px;
}

body {
    margin: 0;
    padding: 0;
    height: 100%;
   
    /* overflow: hidden; hide scrollbar */
}
ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}



