.home {
    height: 100%;
    background: url("../../assets//img/bg.jpg") no-repeat;

}
.home_contain {
    position: relative;
        margin-top: 46px;
        font-size: 1.875rem;
}

.home__overlay {
    position: absolute;
    left: 0;
    right: 0;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
}

.home__intro {
    width: 80%;
}

.home__intro P:first-child {
    color: var(--link-color);
}

.home__intro h2 {
    font-size: 3.125rem;
    font-weight: 500;
}

.angle-down {
    position: absolute;
    left: 50%;
    bottom: 70px;
    display: none;
    width: 150px;
    font-size: 1rem;
    text-align: center;
    transform: translateX(-50%);
    opacity: 0.8;
    transition: all 0.3s ease-in;
    z-index: 99;
}

.angle-down>button {

    padding: 10px 20px;
    color: #fff;
    border: none;
    background-color: transparent;
    cursor: pointer;
}

.home:hover .angle-down {
    display: block;
    animation: blink .5s linear infinite alternate
}


@keyframes blink {
    0% {
        botom: 70px;
    }

    100% {
        bottom: 50px;
    }
}

@media screen and (max-width:768px) {
    .home__intro {
        text-align: center;
    }

}