:root {
    --footer-bg: #222C3C;
    --footer-text-color: #818394;
}
.contact { 
    position: relative;
    text-align: center;
    padding: 0 10px 210px;
    
}
.footer_content {
   
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contact__email {
    text-align: center;
    margin: 20px 0;
}

.contact__email a {
    font-size: 1.5rem;
    color: var(--link-color);
    text-decoration: none;
}

.contact__email a:hover {
    text-decoration: underline;
}

.contact__social {
    display: flex;
    justify-content: center;
    
}
.center {
    display: flex;
        justify-content: center;
        align-items: center;
}
.contact__social a {
    margin: 10px;
    height: 60px;
    line-height: 68px;
    width: 60px;   
    font-size: 2em;
    color: #fff;   
    border-radius: 50%;
    
    


}
.contact__social a:hover {
    opacity: 0.8;
}


.contact__social a:nth-child(1) {
    background-color: #077FF0;
}

.contact__social a:nth-child(2) {
    background-color: #0173B2;
}

.contact__social a:nth-child(3) {
    background-color: #24292F;
}

.contact__footer {
    position: absolute;
    bottom: 0;
    margin-top: auto;
    width: 100%;
    text-align: center;
    padding: 45px;
    color: var(--footer-text-color);
    background-color: var(--footer-bg);
}

@media screen and (max-width:768px) {
    .contact__footer {
        text-align: center;

    }
}