.title {
    margin: 0;
    padding: 2em 0 1em;
    font-size: 2.5em;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
}

.dot {
    display: inline-block;
    width: 10px;
    height: 10px;
    margin-left: 5px;
    background-color: #333;
}

.container {
    max-width: 1200px;
    margin: auto;
}

.wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100vh;
    min-height: 100vh;
    color: #fff;

}

.position_center {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.scrollTop {
    position: fixed;
    bottom: 150px;
    right: 20px;
    width: 90px;
    text-align: center;
    line-height: 40px;
    color: #fff;
    text-transform: uppercase;
    border-radius: 15px;
    background-color: var(--link-color);
    cursor: pointer;
    transition: all 1s;
}

.scrollTop:hover {
    opacity: 0.8;
}

