:root {
    --text-color: #bec273;
}

.about {
    min-height: 100vh;
    background-color: #b5ba64;
}
.about-content {
    display: flex;
    flex-direction: column;
    min-height: inherit;
}
.about-main-wrapper {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about__title {
    color: #fff;
}

.about__main {
    display: flex;
    justify-content: center;
    padding: 0 2em 3em;

}

.about__text__content {
    max-width: 500px;
    color: var(--text-color);
    border: 15px solid #fff;
    min-height: 360px;
    height: auto;
    box-sizing: content-box;
    padding: 0 2em 0;
    overflow: hidden;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    background-color: #333;

}

.about__text__title {
    padding: 10px 0;
    font-size: 1.6em;
    font-weight: 400;
    border-bottom: 1px solid #fff;
    margin-bottom: 10px;
}

.about__text__content>p+p {
    font-size: 1.1em;
    line-height: 1.5em;
    font-family: 'Source Sans Pro', 'sans-serif';
}


.hide {
    display: none;
}


.learnmore {
   position: relative;
    padding: 10px 40px 10px 20px;
    margin: 20px 0 10px;
    font-family: 'Times New Roman';
    color: white;
    font-size: 1.2em;
    border: none;
    background: #3498db;
    background-image: -webkit-linear-gradient(top, #3498db, #2980b9);
    background-image: -moz-linear-gradient(top, #3498db, #2980b9);
    background-image: -ms-linear-gradient(top, #3498db, #2980b9);
    background-image: -o-linear-gradient(top, #3498db, #2980b9);
    background-image: linear-gradient(to bottom, #3498db, #2980b9);
    -webkit-border-radius: 28;
    -moz-border-radius: 28;
    border-radius: 28px;
    text-decoration: none;
    cursor: pointer;
}

.learnmore:hover {
    opacity: 0.8;
}

.btn__arrow {
    position: absolute;
    bottom: 10px;

}

.btn__arrow-up {
    left: 110px
}

.btn__arrow-down {
    left: 100px
}


.about__img_box {
    width: 360px;
    height: 360px;
    border: 15px solid #fff;
    border-radius: 50%;
    background-image: url("../../assets/img/me.jpg");
    background-position: -1 2px;
    background-size: cover;
    background-repeat: no-repeat;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border-left: 0;
    margin-left: -30px;
    transition: all 0.2s;
    box-sizing: content-box;
}

.avatar {

    display: none;
    width: 150px;
    border-radius: 50%;
    border: 6px solid #fff;
    margin-bottom: 20px;
}

.skill {
    color: #42b883;
}


@media screen and (max-width: 992px) {

    .about__main {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 2em 3em;
        flex-direction: column;


    }

    .about__img_box {

        display: none;
    }

    .me {
        display: block;
        float: left;
        max-width: 150px;
        border-radius: 50%;
        border: 6px solid #fff;
        margin-bottom: 10px;

    }

    .about__text__content {
        /* border-color: #fff;  */
        padding: 10px;
        height: auto;
        /* border-width: 6px; */
        border: 6px solid #fff;

    }

    .hide-span {
        display: none;
    }

    .avatar {
        display: block;
    }
    
}