.header {
    position: fixed;
    left: 0;
    top: 0;    
    width: 100%;
    height: 46px;
    background-color: var(--nav-bg);
    z-index: 999;

}
.menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 0px 10px;
    font-size: 1.2em;  

}

.header__nav {
    display: flex;
    list-style: none;
    color: #fff;
    margin-right: 35px;
}
.header__nav li {
    font-size: .75rem;
    padding: 10px;
    cursor: pointer;
}
.header__nav > li + li {
    margin-left: 10px;
}

.header__nav li.active,
.header__nav li:hover {
    color: var(--link-color);
}

.responsive-menu {
    display: none;
}
.header__logo > button {
        padding: 8px 16px;
        bottom: none;
        font-family: 'Meiryo UI' ;
        font-size: 20px;
        color: #fff;
        text-decoration: none;
        letter-spacing: 0.2em;
        background-color: transparent;
        cursor: pointer;
}
.header__logo>button:hover {
    opacity: 0.8;
}

@media (max-width: 768px) {
    .responsive-menu {
        position: fixed;
        left: 0px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        width: 100%;
        height: 46px;
        background-color: var(--nav-bg);
        z-index: 100;
    }

    .responsive-menu .icon {       
        margin-right: 15px;
    }

    .responsive-menu span {
        position: relative;
        display: inline-block;
        padding: 3px 0px;
        height: 20px;
        font-size: .75rem;
        color: #fff;
        
    }

    .responsive-menu span::after {
        content: "";
        position: absolute;
        right: -9px;
        top: 0px;
        display: block;
        height: 20px;
        border-right: 2px solid #fff;

    }
    .icon {
        border: none;
        background-color: transparent;
        margin-left: 15px;
        cursor: pointer;
    }

    .icon img {
        height: 20px;

    }

    .header__nav {
        position: fixed;
        top: 41px;
        right: 15px;
        z-index: 999;
        min-width: 120px;
        background-color: #fff;
        box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.8);
        display: none;
    }
       

    .header__nav li {
        display: block;
        width: 100%;
        color: #333;
        margin-left: 10px;
    }

    .header__logo {
        display: none;
    }

    .show {
        display: block;
    }
    


}