div.portfolio {
    color: #333;
}

.portfolio-center {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.portfolio__content {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0 -10px;
    padding: 5px;
}

.portfolio__content li {
    box-sizing: border-box;
    width: 300px;
    border-radius: 5px;
    padding: 5px;
    
}


@media screen and (max-width:1207px) {
.portfolio__content {
        max-width: 910px;
    }
}
@media screen and (max-width:997px) {
.portfolio__content {
        max-width: 610px;
    }
}
@media screen and (max-width:627px) {
.portfolio__content {
        max-width: 310px;
    }
}
