.card__container {
    display: flex;
    flex-direction: column;
    position: relative;
    height: 100%;
    border-radius: 15px;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);

}
.card-img {
    position: relative;
    height: 158px;
    
}
.card-img img {
    width: 100%;
    height: 100%;
    border-radius: 15px 15px 0 0 ;
}
.card-content {
    padding: 10px;
}
.card-desc p {
    margin: 5PX 0;
}

.card__container a {
    display: block;
    height: 100%;
    text-decoration: none;
    color: #fff
}

.card__container a:active,
.card__container a:visited {
    color: #fff
}



.card__overlay {
    position: absolute;
    box-sizing: border-box;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    overflow: hidden;
    width: 100%;
    height: 100%;
    opacity: 0;
    padding: 20px;
    color: white;
    transition: all 0.3s ease;
    border-radius: 15px 15px 0 0;

}


.card__content {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

}

.card__link {
    position: absolute;
    font-size: 1.6em;
    top: 50%;
    cursor: pointer;
    transition: all 1s ease;
}

.github {
    left: -50px;
}

.live {
    right: -50px;
}

.card__link:hover {
    opacity: 0.6;
}

.card__container:hover .card__overlay {
    opacity: 1;

}

.card__container:hover .live {
    right: calc(45% - 40px);

}

.card__container:hover .github {
    left: calc(45% - 40px);

}